.messageBlock{
    align-self: flex-start;
    margin-bottom: 20px;
}
.messageBlock.mine{
    align-self: flex-end;
}
.inner{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.inner.reverse{
    flex-direction: row-reverse;
}
.message{
    margin-left: 5px;
    max-width: 500px;
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 30px;
    text-align: center;
    line-height: 1.5;
}
.message.mine{
    margin-left: 0;
    margin-right: 5px;
    background-color: var(--main-color-light);
    color: #fff;
}
.message:last-child{
    margin-bottom: 0;
}
.author{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-transform: uppercase;
    height: 30px;
    width: 30px;
    background-color: #333333;
    border-radius: 50%;
}
